import React from 'react';
import { FaFacebookSquare , FaInstagramSquare , FaGithubSquare, FaBars, FaWindowClose} from 'react-icons/fa';
import { ImLinkedin, ImHome , ImProfile, ImHome2} from 'react-icons/im';
import { GrContactInfo, GrServices} from 'react-icons/gr';
import {NavLink} from 'react-router-dom';

function open(){
  document.querySelector(".nav").classList.toggle("nav-open");;
}


function Navbar(){
return(
<>
<FaWindowClose className="mob-icon close"/>
  <FaBars className="mob-icon open" onClick={open}/>
  <ul  className="nav">

  <div className="branding">
  <NavLink className="brand" to="/">
    <span>K</span>hagesh<span>B</span>ansal
    </NavLink>
    </div>

  <div className="links">
    <li  className="">
      <NavLink to="/" className=""><ImHome className='nav-ico'/>Home</NavLink>
    </li>
    <li>
      <NavLink to="/about"  className=""><GrContactInfo className='nav-ico'/>About</NavLink>
    </li>
    <li>
      <NavLink to="/profile"  className=""><ImProfile className='nav-ico'/>Profile</NavLink>
    </li>
    <li>
      <NavLink to="/service"  className=""><GrServices className='nav-ico'/>Services</NavLink>
    </li>
    </div>

    <div className="icons">
    <FaFacebookSquare className='iccon'/><FaInstagramSquare className='iccon'/> < FaGithubSquare className='iccon'/> <ImLinkedin className='iccon'/>
      </div>
  </ul>
  </>
);
}
export default Navbar;